.gifs-card{
    align-self: center;
    border: 2px solid #eeeeee;
    border-radius: 5px;
    height: auto;
    justify-self: center;
    min-height: 100%;
    width: 100%;    
}
.gifs-card>img {
    display: block;
    height: 200px;
    margin-left:auto;
    margin-right: auto;
    max-width: 100%;
}
.gifs-card>figcaption {
    text-align:center;
    padding: 10px;
    text-transform: capitalize;
}
